import React from "react";
import Moment from 'react-moment';

import './style.css';

class GraphCardInformation extends React.Component {
  render(props) {
    return (
        <a className="ui card graphCardInformation">
        <div className="content">
            <div className="left">
                <img src={this.props.img} alt=""/>
            </div>
            <div className="right">
                <span className="meta">{this.props.title}</span>
                <h6>{this.props.value}ºC</h6>
                {this.props.colocaData === "true" &&
                    <p><Moment format="DD/MM/YYYY">{this.props.date}</Moment></p>
                }
            </div>
         </div>
        </a>
    );
  }
}

export default GraphCardInformation;
