import React from "react";
import { NavLink } from "react-router-dom";
import axios from 'axios';
import Moment from 'react-moment';
import moment from "moment";

import DividerSimple from './../../../../Components/DividerSimple';

import GraphCardInformation from './../Components/GraphCardInformation';

import GraphLine from './../Components/VictoryLine';

import img from '../../../../../assets/img/icons/talhao.png';
import imgSemantic from './../../../../../assets/img/short-paragraph.png';

import './style.css';

import dadosJson from './dados.json';

// Set the locale for every react-moment instance to French.
Moment.globalLocale = 'pt-br';

class GraficosPrecipitacao extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loader: {display: 'block',opacity: '1',text: 'Construindo os gráficos...'},
            DataDeHoje: '2019-05-03',
            titleGraphAtual: 'Dados de hoje',
            dadosDoGrafico: [],
            dadosDe5dias: [],
            dadosDe10dias: [],
            dadosDe30dias: [],
            dadosPersonalizados: [],
            dataInicial:moment().subtract(30, 'days').format("YYYY-MM-DD"),
            dataFinal:moment().subtract(0, 'days').format("YYYY-MM-DD"),
            temperaturaMinima: 99,
            temperaturaMinimaData: moment().subtract(0, 'days').format("YYYY-MM-DD"),
            temperaturaMedia: 0,
            temperaturaMaxima: 0,
            temperaturaMaximaData: moment().subtract(0, 'days').format("YYYY-MM-DD"),
        };
    }

    async componentDidMount() {
        console.log('Preparando Dados de 30 Dias');
        this.dadosDosUltimos30Dias();

        var promise = new Promise(resolve => setTimeout(() => {
            let prepara = "vai!" ;
            if (prepara === "vai!") {
             resolve("Promise resolved successfully");
            }
        }, 2000));

        promise.then( result => {
            console.log('Preparando Dados de 5 Dias');
            this.dadosDosUltimos5Dias();
            console.log('Preparando Dados de 10 Dias');
            this.dadosDosUltimos10Dias();
            this.setState({
                loader: {
                    display: 'none',
                    opacity: '0',
                    text: 'Pronto!'
                }
            });
            this.botao5dias();
         });

        await new Promise(resolve => setTimeout(resolve, 3000)); // 3 sec
    }

    dadosDosUltimos5Dias = () => {
        var newArray = this.state.dadosDe30dias.slice(0,5);
        this.setState({
            dadosDe5dias: newArray
        })
    }

    dadosDosUltimos10Dias = () => {
        var newArray = this.state.dadosDe30dias.slice(0,10);
        this.setState({
            dadosDe10dias: newArray
        })
    }

    dadosDosUltimos30Dias = async () => {
        this.setState({
            dadosDe30dias: dadosJson
        });
    }

    botao5dias = () => {
        this.setState({
            dadosDoGrafico: this.state.dadosDe5dias,
            titleGraphAtual: "Gráfico dos últimos 5 dias",
            temperaturaMinima: 99,
            temperaturaMaxima: 0
        })
        var promise5 = new Promise(resolve => setTimeout(() => {
            let prepara = "vai!" ;
            if (prepara === "vai!") {
             resolve("Promise resolved successfully");
            }
        }, 1000));
        promise5.then( result => {
            let aux = 0;
            let count = 0;
            const tamanhoArray = this.state.dadosDe5dias.length;
            this.state.dadosDe5dias.map((dado, i) => {
                aux = aux + dado.temperatura;
                count++;
                if(dado.temperatura < this.state.temperaturaMinima){
                    this.setState({
                        temperaturaMinima: dado.temperatura,
                        temperaturaMinimaData: dado.data
                    })
                }
                if(dado.temperatura > this.state.temperaturaMaxima){
                    this.setState({
                        temperaturaMaxima: dado.temperatura,
                        temperaturaMaximaData: dado.data
                    })
                }
                if(tamanhoArray === i + 1){
                    this.setState({
                        temperaturaMedia: (aux/count).toFixed(2)
                    });
                }
            })
         });
    }

    botao10dias = () => {
        this.setState({
            dadosDoGrafico: this.state.dadosDe10dias,
            titleGraphAtual: "Gráfico dos últimos 10 dias",
            temperaturaMinima: 99,
            temperaturaMaxima: 0
        })
        var promise10 = new Promise(resolve => setTimeout(() => {
            let prepara = "vai!" ;
            if (prepara === "vai!") {
             resolve("Promise resolved successfully");
            }
        }, 1000));
        promise10.then( result => {
            let aux = 0;
            let count = 0;
            const tamanhoArray = this.state.dadosDe10dias.length;
            this.state.dadosDe10dias.map((dado, i) => {
                aux = aux + dado.temperatura;
                count++;
                if(dado.temperatura < this.state.temperaturaMinima){
                    this.setState({
                        temperaturaMinima: dado.temperatura,
                        temperaturaMinimaData: dado.data
                    })
                }
                if(dado.temperatura > this.state.temperaturaMaxima){
                    this.setState({
                        temperaturaMaxima: dado.temperatura,
                        temperaturaMaximaData: dado.data
                    })
                }
                if(tamanhoArray === i + 1){
                    this.setState({
                        temperaturaMedia: (aux/count).toFixed(2)
                    });
                }
            })
         });
    }

    botao30dias = () => {
        this.setState({
            dadosDoGrafico: this.state.dadosDe30dias,
            titleGraphAtual: "Gráfico dos últimos 30 dias",
            temperaturaMinima: 99,
            temperaturaMaxima: 0
        })
        var promise30 = new Promise(resolve => setTimeout(() => {
            let prepara = "vai!" ;
            if (prepara === "vai!") {
             resolve("Promise resolved successfully");
            }
        }, 1000));
        promise30.then( result => {
            let aux = 0;
            let count = 0;
            const tamanhoArray = this.state.dadosDe30dias.length;
            this.state.dadosDe30dias.map((dado, i) => {
                aux = aux + dado.temperatura;
                count++;
                if(dado.temperatura < this.state.temperaturaMinima){
                    this.setState({
                        temperaturaMinima: dado.temperatura,
                        temperaturaMinimaData: dado.data
                    })
                }
                if(dado.temperatura > this.state.temperaturaMaxima){
                    this.setState({
                        temperaturaMaxima: dado.temperatura,
                        temperaturaMaximaData: dado.data
                    })
                }
                if(tamanhoArray === i + 1){
                    this.setState({
                        temperaturaMedia: (aux/count).toFixed(2)
                    });
                }
            })
         });
    }

    onChangeDataInicial(event) {
        this.setState({
            dataInicial:event.target.value
        });
    }
    onChangeDataFinal(event) {
        this.setState({
            dataFinal:event.target.value
        });
    }

    botaoPersonalizado = () => {
        this.setState({
            temperaturaMinima: 99,
            temperaturaMaxima: 0
        })
        let dataInicial = this.state.dataInicial;
        let dataFinal = this.state.dataFinal;
        axios.get(`http://localhost:8000/api/temperaturas_personalizadas/${dataInicial}/${dataFinal}`)
            .then(res => {
                this.setState({
                    dadosPersonalizados: res.data,
                    dadosDoGrafico: res.data,
                    titleGraphAtual: "Gráfico Personalizado"
                });
        });
        var promiseP = new Promise(resolve => setTimeout(() => {
            let prepara = "vai!" ;
            if (prepara === "vai!") {
             resolve("Promise resolved successfully");
            }
        }, 3000));
        promiseP.then( result => {
            let aux = 0;
            let count = 0;
            const tamanhoArray = this.state.dadosPersonalizados.length;
            this.state.dadosPersonalizados.map((dado, i) => {
                aux = aux + dado.temperatura;
                count++;
                if(dado.temperatura < this.state.temperaturaMinima){
                    this.setState({
                        temperaturaMinima: dado.temperatura,
                        temperaturaMinimaData: dado.data
                    })
                }
                if(dado.temperatura > this.state.temperaturaMaxima){
                    this.setState({
                        temperaturaMaxima: dado.temperatura,
                        temperaturaMaximaData: dado.data
                    })
                }
                if(tamanhoArray === i + 1){
                    this.setState({
                        temperaturaMedia: (aux/count).toFixed(2)
                    });
                }
            })
         });
    }


  render() {
    return (
      <div id="page-graph-precipitacao" className="ui container">
        <div className="row">
	      	<div className="ui breadcrumb">
	      	<NavLink to="/fazendas" className="section">Minhas Fazendas</NavLink>
	      	<i aria-hidden="true" className="right angle icon divider"></i>
            <NavLink to="/fazendas/fazenda" className="section">Fazenda Raio de Sol</NavLink>
            <i aria-hidden="true" className="right angle icon divider"></i>
	      	<div className="active section">Gráficos de Precipitação</div>
	      	</div>
        </div>
        <div className="row">
            <div className="ui center aligned container">
            	<h1 className="title">Dados de Precipitação</h1>
            	<DividerSimple />
            </div>
        </div>
        <div className="row">
            <div className="ui cards cards-informations">
                <GraphCardInformation img={img} title="Temperatura Mínima" value={this.state.temperaturaMinima} date={this.state.temperaturaMinimaData} colocaData="true"/>
                <GraphCardInformation img={img} title="Temperatura Média" value={this.state.temperaturaMedia} colocaData="false"/>
                <GraphCardInformation img={img} title="Temperatura Máxima" value={this.state.temperaturaMaxima} date={this.state.temperaturaMaximaData} colocaData="true"/>
            </div>
        </div>


        <div className="row">
            <div className="ui segment dimmable">
                <div style={{ display: this.state.loader.display, opacity: this.state.loader.opacity }} className="ui inverted dimmer">
                    <div className="content">
                        <div className="ui text loader">
                            {this.state.loader.text}
                        </div>
                    </div>
                </div>
                <div className="graphHeader">
                    <div className="actionsButtons">
                        <button onClick={this.botao5dias} className="ui vertical animated button">
                            <div className="hidden content">
                                <i aria-hidden="true" className="chart line icon"></i>
                            </div>
                            <div className="visible content">
                                5 Dias
                            </div>
                        </button>
                        <button onClick={this.botao10dias} className="ui vertical animated button">
                            <div className="hidden content">
                                <i aria-hidden="true" className="chart line icon"></i>
                            </div>
                            <div className="visible content">
                                10 Dias
                            </div>
                        </button>
                        <button onClick={this.botao30dias} className="ui vertical animated button">
                            <div className="hidden content">
                                <i aria-hidden="true" className="chart line icon"></i>
                            </div>
                            <div className="visible content">
                                30 Dias
                            </div>
                        </button>
                        <div className="ui labeled input">
                            <div className="ui label label">De</div>
                            <input onChange={this.onChangeDataInicial.bind(this)} value={this.state.dataInicial} type="text" placeholder="Data Inicial"/>
                        </div>
                        <div className="ui labeled input">
                            <div className="ui label label">Até</div>
                            <input onChange={this.onChangeDataFinal.bind(this)} value={this.state.dataFinal}  type="text" placeholder="Data Final"/>
                        </div>
                        <button className="ui vertical animated button">
                            <div className="hidden content">
                                <i aria-hidden="true" className="chart line icon"></i>
                            </div>
                            <div className="visible content">
                                Personalizado
                            </div>
                        </button>
                    </div>
                    <div className="title">{this.state.titleGraphAtual}</div>
                </div>
                <div className="graphBody">
                    <GraphLine data={this.state.dadosDoGrafico} />
                </div>
            </div>
        </div>
      </div>
    );
  }
}

export default GraficosPrecipitacao;
