import React from "react";
import ReactDOM from 'react-dom';
import { VictoryChart, VictoryLine, VictoryVoronoiContainer, VictoryTooltip, VictoryAxis } from 'victory';

const tickStyle = {
    axis: {
      stroke: 'black',
      strokeOpacity: 0
    },
    ticks: {
      size: 2,
      stroke: 'black',
      strokeOpacity: 0.1
    },
    grid: {
      stroke: 'rgba(0, 0, 0, 0.1)',
      strokeWidth: 1,
      strokeDasharray: '6, 6',
    },
    tickLabels: {
      fontSize: '9px',
      fontFamily: 'inherit',
      fillOpacity: 1,
      margin: 0,
      padding: 0
    },
    axisLabel: {
      fontsize: 13
    }
  };

class GraphLine extends React.Component {
      render(props) {
        return (
            <VictoryChart
                animate={{ duration: 1000, easing: "linear" }}
                minDomain={{ y: 10 }}
                maxDomain={{ y: 40 }}
                domainPadding={{x: [20, 0],y: [50, 0]}}
                containerComponent={<VictoryVoronoiContainer />}
            >
            <VictoryAxis
                dependentAxis
                tickFormat={t => `${Math.round(t)}ºC`}
                style={{
                  axis: {
                    stroke: '#303030'
                  },
                  ticks: {
                    stroke: '#321452'
                  },
                  tickLabels: {
                    color: "#321452",
                    fill: "#321321",
                    fontSize: '10px'
                  }
                }}
                standalone={false}
            />
            <VictoryAxis
                standalone={false}
                style={{
                  axis: {
                    stroke: '#303030'
                  },
                  ticks: {
                    stroke: '#321452'
                  },
                  tickLabels: {
                    color: "#321452",
                    fill: "#321321",
                    fontSize: '8px'
                  }
                }}
              />
            <VictoryLine
                style={{
                    data: {
                        stroke: "#2A58AD",
                        strokeWidth: 1,
                    },
                    parent: { border: "1px solid #303030"}
                }}
                data={this.props.data}
                x="data"
                y="temperatura"
                labels={d => `Temperatura: ${d.temperatura}ºC, Data: ${d.data}`}
                labelComponent={
                    <VictoryTooltip
                      flyoutStyle={{
                        stroke: (d) => d.x === 5 ?
                          "tomato" : "#2A58AD"
                      }}
                    />
                }
            />
            </VictoryChart>
        );
      }
}

export default GraphLine;
